.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
img{
  width:600px;
  height: 400px;
}
button{
  padding:2px;
  margin:4px;
  width: 75px;
  height: 40px;
  border-radius: 5px;
  background-color: rgb(132, 124, 132);
  color:black
}
.left:hover{
  background-color: rgb(190, 75, 75);
}
.right:hover{
  background-color: rgb(21, 134, 59);
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
